import { Component, Inject, OnDestroy, OnInit } from '@angular/core';
import { FormControl, FormGroup } from '@angular/forms';
import { TranslateService } from '@ngx-translate/core';
import { BreadCrumb, SnackBarService } from '@tei/material';
import { Subscription } from 'rxjs';
import { IGlobalStylesheetService } from '../../../../@intermediate/global-stylesheet.interface';
import
{
  GetGlobalStyleSheetRequest,
  GetGlobalStyleSheetResponse
} from '../../../../@intermediate/models/get-global-stylesheet.model';
import
{
  PostGlobalStyleSheetRequest,
  PostGlobalStyleSheetResponse
} from '../../../../@intermediate/models/post-global-stylesheet.model';

@Component({
  selector: 'tei-global-stylesheet-page',
  templateUrl: './global-stylesheet-page.component.html',
  styleUrls: ['./global-stylesheet-page.component.scss']
})
export class GlobalStylesheetPageComponent implements OnInit, OnDestroy
{
  form = new FormGroup({
    Stylesheet: new FormControl('')
  });

  breadCrumbData: BreadCrumb[] = [];
  isLoading: boolean = false;
  subscription = new Subscription();

  editorOptions = {
    language: 'css',
    automaticLayout: true,
    minimap: { enabled: false }
  };

  constructor(
    private snackBarService: SnackBarService,
    private translate: TranslateService,
    @Inject('IGlobalStylesheetService') private service: IGlobalStylesheetService
  ) { }

  ngOnInit(): void
  {
    this.initializeBreadcrumb();
    this.getThemeData();
  }

  ngOnDestroy(): void
  {
    this.subscription.unsubscribe();
  }

  get formControls()
  {
    return this.form.controls;
  }

  private initializeBreadcrumb(): void
  {
    this.breadCrumbData = [
      {
        title: this.translate.instant('MARKETING_CMS_ADMIN.MANAGE_USER_THEME.MARKETING'),
        relativeUrl: ''
      },
      {
        title: this.translate.instant('MARKETING_CMS_ADMIN.MANAGE_USER_THEME.CONTENT_MANAGEMENT'),
        relativeUrl: '../../'
      },
      {
        title: this.translate.instant('MARKETING_CMS_ADMIN.MANAGE_USER_THEME.THEME'),
        relativeUrl: '../'
      },
      {
        title: this.translate.instant('MARKETING_CMS_ADMIN.MANAGE_USER_THEME.GLOBAL_STYLESHEET_BTN'),
        relativeUrl: ''
      }
    ];
  }

  private getThemeData(): void
  {
    this.isLoading = true;
    const request = new GetGlobalStyleSheetRequest();

    this.subscription.add(
      this.service.Get(request).subscribe({
        next: (response: GetGlobalStyleSheetResponse) =>
        {
          if (response && response.Stylesheet)
          {
            this.form.patchValue({
              Stylesheet: response.Stylesheet
            });
          }
          this.isLoading = false;
        },
        error: () =>
        {
        }
      })
    );
  }

  onSave(): void
  {
    this.isLoading = true;
    const formValue = this.form.value;

    const request: PostGlobalStyleSheetRequest = {
      Stylesheet: formValue.Stylesheet || ''
    };

    this.subscription.add(
      this.service.Post(request).subscribe({
        next: (response: PostGlobalStyleSheetResponse) =>
        {
          this.isLoading = false;
          this.snackBarService.showSuccess(
            this.translate.instant("MARKETING_CMS_ADMIN.MANAGE_PAGE_DIALOG.STYLESHEET_SAVED"),
            '',
            null,
            'left'
          );
        },
        error: () =>
        {
        }
      })
    );
  }
}
