import { Inject, Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { IGlobalStylesheetService } from '../@intermediate/global-stylesheet.interface';
import {
  GetGlobalStyleSheetRequest,
  GetGlobalStyleSheetResponse
} from '../@intermediate/models/get-global-stylesheet.model';
import {
  PostGlobalStyleSheetRequest,
  PostGlobalStyleSheetResponse
} from '../@intermediate/models/post-global-stylesheet.model';
import { IAPIBaseUrlProvider } from '@tei/core';

@Injectable({
  providedIn: 'root'
})
export class GlobalStylesheetService implements IGlobalStylesheetService {

  private readonly serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, private http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Marketing/CMS/Admin/Theme/GlobalStyleSheet';
  }

  Get(request: GetGlobalStyleSheetRequest): Observable<GetGlobalStyleSheetResponse>
  {
    const apiUrl = this.serviceUrl;
    return this.http.get<GetGlobalStyleSheetResponse>(apiUrl, { params: request as any });
  }

  Post(request: PostGlobalStyleSheetRequest): Observable<PostGlobalStyleSheetResponse> {
  {
    const apiUrl = this.serviceUrl;
    return this.http.post<PostGlobalStyleSheetResponse>(apiUrl, request);
  }
 }
}
