import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { IManageUserThemeService, PostUserThemeRequest } from '../@intermediate/manage-user-theme.interface';
import { GetUserThemeRequest, GetUserThemeResponse } from '../@intermediate/models/get-user-theme.model';
import { PostUserThemeResponse } from '../@intermediate/models/post-user-theme.model';

@Injectable({
  providedIn: 'root'
})
export class ManageUserThemeService implements IManageUserThemeService
{
  private readonly serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, private http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Marketing/CMS/Admin/Theme';
  }

  Get(request: GetUserThemeRequest): Observable<GetUserThemeResponse>
  {
    const apiUrl = this.serviceUrl;
    return this.http.get<GetUserThemeResponse>(apiUrl, { params: request as any });
  }

  Post(request: PostUserThemeRequest): Observable<PostUserThemeResponse>
  {
    const apiUrl = this.serviceUrl;
    return this.http.post<PostUserThemeResponse>(apiUrl, request);
  }
}
