import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { GlobalStylesheetPageComponent } from './global-stylesheet-page/global-stylesheet-page.component';
import { ManageUserThemeComponent } from './manage-user-theme.component';

const routes: Routes = [
  { path: '', component: ManageUserThemeComponent },
  { path: 'global-css-stylesheet', component: GlobalStylesheetPageComponent }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class ManageUserThemeRoutingModule
{
}
