import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { ReactiveFormsModule } from '@angular/forms';
import { MatLegacyButtonModule as MatButtonModule } from '@angular/material/legacy-button';
import { MatLegacyCardModule as MatCardModule } from '@angular/material/legacy-card';
import { MatLegacyCheckboxModule as MatCheckboxModule } from '@angular/material/legacy-checkbox';
import { MatLegacyFormFieldModule as MatFormFieldModule } from '@angular/material/legacy-form-field';
import { MatLegacySelectModule as MatSelectModule } from '@angular/material/legacy-select';
import { MatSnackBarModule } from '@angular/material/snack-bar';
import { RouterOutlet } from '@angular/router';
import { TeiTextTranslatePipeModule } from '@tei/core-translate';
import { BreadCrumbModule, LoadingModule } from '@tei/material';
import { ManageUserThemeService } from '../../../@services/manage-user-theme.service';
import { ManageUserThemeRoutingModule } from './manage-user-theme-routing.module';
import { ManageUserThemeComponent } from './manage-user-theme.component';
import { GlobalStylesheetPageModule } from './global-stylesheet-page/global-stylesheet-page.module';

const BASE_MODULES = [CommonModule, ReactiveFormsModule, ManageUserThemeRoutingModule];
const MATERIAL_MODULES = [MatCardModule, MatFormFieldModule, MatSelectModule, MatCheckboxModule, MatSnackBarModule, MatButtonModule];
const TEI_MATERIAL_MODULES = [LoadingModule, BreadCrumbModule, GlobalStylesheetPageModule];
const TEI_CORE_MODULES = [TeiTextTranslatePipeModule];

@NgModule({
  declarations: [ManageUserThemeComponent],
  imports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...TEI_CORE_MODULES,
    RouterOutlet
  ],
  providers: [{ provide: 'IManageUserThemeService', useClass: ManageUserThemeService }],
})
export class ManageUserThemeModule { } 
