import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { ReactiveFormsModule } from '@angular/forms';
import { MatLegacyButtonModule as MatButtonModule } from '@angular/material/legacy-button';
import { MatLegacyCardModule as MatCardModule } from '@angular/material/legacy-card';
import { MatLegacyFormFieldModule as MatFormFieldModule } from '@angular/material/legacy-form-field';
import { MatSnackBarModule } from '@angular/material/snack-bar';
import { TeiTextTranslatePipeModule } from '@tei/core-translate';
import { BreadCrumbModule, LoadingModule, MaterialCodeEditorModule } from '@tei/material';
import { GlobalStylesheetService } from '../../../../@services/global-stylesheet.service';
import { GlobalStylesheetPageComponent } from './global-stylesheet-page.component';

const BASE_MODULES = [CommonModule, ReactiveFormsModule, MaterialCodeEditorModule];
const MATERIAL_MODULES = [MatCardModule, MatFormFieldModule, MatSnackBarModule, MatButtonModule];
const TEI_MATERIAL_MODULES = [LoadingModule, BreadCrumbModule];
const TEI_CORE_MODULES = [TeiTextTranslatePipeModule];
const COMPONENTS = [GlobalStylesheetPageComponent];
@NgModule({
  declarations: [...COMPONENTS],
  imports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...TEI_CORE_MODULES,
  ],
  providers: [{ provide: 'IGlobalStylesheetService', useClass: GlobalStylesheetService }],
})
export class GlobalStylesheetPageModule { }
