import { Component, Inject, OnDestroy, OnInit } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { BreadCrumb, SnackBarService } from '@tei/material';
import { Subscription } from 'rxjs';
import { IManageUserThemeService } from '../../../@intermediate/manage-user-theme.interface';
import { GetUserThemeRequest, GetUserThemeResponse, ThemeOption } from '../../../@intermediate/models/get-user-theme.model';
import { PostUserThemeRequest, PostUserThemeResponse } from '../../../@intermediate/models/post-user-theme.model';

export enum ThemeKey
{
  MaterialDark = 'material-dark',
  MaterialLight = 'material-light'
}

export enum ThemeValue
{
  Dark = 'dark',
  Light = 'light',
  Sombre = 'sombre',
  Lumiere = 'lumière'
}

export enum TranslationKey
{
  DarkTheme = 'MARKETING_CMS_ADMIN.MANAGE_USER_THEME.DARK_THEME',
  LightTheme = 'MARKETING_CMS_ADMIN.MANAGE_USER_THEME.LIGHT_THEME'
}

export interface WebTheme
{
  value: string;
  label: string;
}

@Component({
  selector: 'tei-manage-user-theme',
  templateUrl: './manage-user-theme.component.html',
  styleUrls: ['./manage-user-theme.component.scss']
})
export class ManageUserThemeComponent implements OnInit, OnDestroy
{
  subscription = new Subscription();

  form = new FormGroup({
    EnforceOneWebTheme: new FormControl(false),
    WebTheme: new FormControl<string>(ThemeValue.Dark)
  });

  breadCrumbData: BreadCrumb[] = [];
  isLoading: boolean = false;
  webThemes: WebTheme[] = [];
  themeOptions: ThemeOption[] = [];

  constructor(
    private snackBarService: SnackBarService,
    private translate: TranslateService,
    private router: Router,
    private route: ActivatedRoute,
    @Inject('IManageUserThemeService') private service: IManageUserThemeService
  ) { }

  ngOnInit(): void
  {
    this.initializeForm();
    this.initializeBreadcrumb();
    this.getThemeData();
  }

  ngOnDestroy(): void
  {
    this.subscription.unsubscribe();
  }

  private initializeForm(): void
  {
    // Set up form validation
    this.form.get('WebTheme')?.setValidators([
      Validators.required
    ]);

    // Update validation when EnforceOneWebTheme changes
    this.form.get('EnforceOneWebTheme')?.valueChanges.subscribe(value =>
    {
      if (value)
      {
        this.form.get('WebTheme')?.setValidators([Validators.required]);
      } else
      {
        this.form.get('WebTheme')?.clearValidators();
      }
      this.form.get('WebTheme')?.updateValueAndValidity();
    });
  }

  private initializeBreadcrumb(): void
  {
    this.breadCrumbData = [
      {
        title: this.translate.instant('MARKETING_CMS_ADMIN.MANAGE_USER_THEME.MARKETING'),
        relativeUrl: ''
      },
      {
        title: this.translate.instant('MARKETING_CMS_ADMIN.MANAGE_USER_THEME.CONTENT_MANAGEMENT'),
        relativeUrl: '../'
      },
      {
        title: this.translate.instant('MARKETING_CMS_ADMIN.MANAGE_USER_THEME.THEME'),
        relativeUrl: ''
      }
    ];
  }

  onEnforceOneWebThemeChange(): void
  {
    const enforceValue = this.form.get('EnforceOneWebTheme')?.value;
    if (!enforceValue)
    {
      // Reset WebTheme to default when enforce is disabled
      this.form.get('WebTheme')?.setValue(ThemeValue.Dark);
    }
  }

  private getThemeData(): void
  {
    this.isLoading = true;
    const request = new GetUserThemeRequest();

    this.subscription.add(this.service.Get(request).subscribe({
      next: (response: GetUserThemeResponse) =>
      {
        if (response)
        {
          this.themeOptions = response.Options || [];
          this.populateWebThemes();

          // Set the current theme if available
          if (response.WebTheme !== null)
          {
            // Check the "Enforce one web theme for all users" checkbox
            this.form.get('EnforceOneWebTheme')?.setValue(true);

            // Now WebTheme is a string, so we can directly use it
            const selectedTheme = this.themeOptions.find(option => option.Key === response.WebTheme);
            if (selectedTheme)
            {
              // Map API values to form values
              let formValue: string = ThemeValue.Dark; // Default fallback
              if (selectedTheme.Value.toLowerCase() === ThemeValue.Dark || selectedTheme.Value.toLowerCase() === ThemeValue.Sombre)
              {
                formValue = ThemeValue.Dark;
              } else if (selectedTheme.Value.toLowerCase() === ThemeValue.Light || selectedTheme.Value.toLowerCase() === ThemeValue.Lumiere)
              {
                formValue = ThemeValue.Light;
              } else
              {
                // For unknown values, try to map to known enum values or use default
                const lowerValue = selectedTheme.Value.toLowerCase();
                if (Object.values(ThemeValue).includes(lowerValue as ThemeValue))
                {
                  formValue = lowerValue;
                } else
                {
                  formValue = ThemeValue.Dark; // Default fallback
                }
              }

              this.form.get('WebTheme')?.setValue(formValue);
            }
          }
        }
        this.isLoading = false;
      },
      error: (error) =>
      {
        this.snackBarService.showError(this.translate.instant('MARKETING_CMS_ADMIN.MANAGE_USER_THEME.LOAD_ERROR'), '', null, 'left');
        this.isLoading = false;
      }
    }));
  }

  private populateWebThemes(): void
  {
    this.webThemes = this.themeOptions
      .filter(option => option.Key && option.Key.trim() !== '') // Filter out invalid options (empty or null keys)
      .map(option =>
      {
        // Map API values to translation keys
        let translationKey = '';
        if (option.Value.toLowerCase() === ThemeValue.Dark || option.Value.toLowerCase() === ThemeValue.Sombre)
        {
          translationKey = TranslationKey.DarkTheme;
        } else if (option.Value.toLowerCase() === ThemeValue.Light || option.Value.toLowerCase() === ThemeValue.Lumiere)
        {
          translationKey = TranslationKey.LightTheme;
        } else
        {
          // Fallback to direct mapping
          translationKey = `MARKETING_CMS_ADMIN.MANAGE_USER_THEME.${ option.Value.toUpperCase() }_THEME`;
        }

        // Map API values to form values
        let formValue: string = ThemeValue.Dark; // Default fallback
        if (option.Value.toLowerCase() === ThemeValue.Dark || option.Value.toLowerCase() === ThemeValue.Sombre)
        {
          formValue = ThemeValue.Dark;
        } else if (option.Value.toLowerCase() === ThemeValue.Light || option.Value.toLowerCase() === ThemeValue.Lumiere)
        {
          formValue = ThemeValue.Light;
        } else
        {
          // For unknown values, try to map to known enum values or use default
          const lowerValue = option.Value.toLowerCase();
          if (Object.values(ThemeValue).includes(lowerValue as ThemeValue))
          {
            formValue = lowerValue;
          } else
          {
            formValue = ThemeValue.Dark; // Default fallback
          }
        }

        return {
          value: formValue,
          label: this.translate.instant(translationKey)
        };
      });
  }

  onSave(): void
  {
    if (this.form.valid)
    {
      this.isLoading = true;
      const formData = this.form.value;

      let themeKey: string | null = null;

      // Only set theme key if "Enforce one web theme for all users" is checked
      if (formData.EnforceOneWebTheme)
      {
        // Map form values back to API keys
        if (formData.WebTheme === ThemeValue.Dark)
        {
          // Find the dark theme key (could be "Dark" or "Sombre")
          themeKey = this.themeOptions.find(option =>
            option.Value.toLowerCase() === ThemeValue.Dark || option.Value.toLowerCase() === ThemeValue.Sombre
          )?.Key || ThemeKey.MaterialDark;
        }
        else if (formData.WebTheme === ThemeValue.Light)
        {
          // Find the light theme key (could be "Light" or "Lumière")
          themeKey = this.themeOptions.find(option =>
            option.Value.toLowerCase() === ThemeValue.Light || option.Value.toLowerCase() === ThemeValue.Lumiere
          )?.Key || ThemeKey.MaterialLight;
        }
        else
        {
          // Fallback to direct mapping
          themeKey = this.themeOptions.find(option =>
            option.Value.toLowerCase() === formData.WebTheme
          )?.Key || ThemeKey.MaterialLight;
        }
      }

      const request: PostUserThemeRequest = {
        WebTheme: themeKey
      };

      this.subscription.add(this.service.Post(request).subscribe({
        next: (response: PostUserThemeResponse) =>
        {
          this.isLoading = false;
          this.snackBarService.showSuccess(this.translate.instant('MARKETING_CMS_ADMIN.MANAGE_USER_THEME.SAVE_SUCCESS'), '', null, 'left');
        },
        error: (error) =>
        {
          console.error('Error saving theme data:', error);
          this.snackBarService.showError(this.translate.instant('MARKETING_CMS_ADMIN.MANAGE_USER_THEME.SAVE_ERROR'), '', null, 'left');
          this.isLoading = false;
        }
      }));
    }
  }

  onGlobalStyleSheetDialog(): void
  {
    this.router.navigate(['global-css-stylesheet'], { relativeTo: this.route });
  }
}
